
/* 
*/
export class AdminActivityDefaultCreateRequest {
 
    // Year (4 digits) 
    public year: number;
 
    // Month (1-12) 
    public month: number;
 
    // Day (1-31), 0 means create month activities 
    public day: number;
 
    constructor(year?: number, month?: number, day?: number) { 
        this.year = year;
        this.month = month;
        this.day = day;
    }

}


/*
 *  Interface of AdminActivityDefaultCreateRequest
*/
export interface IAdminActivityDefaultCreateRequest {
 
    // Year (4 digits) 
    year?: number;
 
    // Month (1-12) 
    month?: number;
 
    // Day (1-31), 0 means create month activities 
    day?: number;
 
}
