import { BaseEntity } from '../entities/BaseEntity';
import { IBaseEntity } from '../entities/BaseEntity';

/* 
 *  Actual activity for booking
*/
export class Actual extends BaseEntity {
 
    // Account Id 
    public accountId: string;
 
    // Booking Id 
    public bookingId: string;
 
    // User Id who registered for booking 
    public userId: string;
 
    // User name (for display only) 
    public userName: string;
 
    // Resource Id 
    public resourceId: string;
 
    // Resource name (for display only) 
    public resourceName: string;
 
    // Actual Duration (in minutes) 
    public duration: number;
 
    // Actual Distance (in meters) 
    public distance: number;
 
    // User missed the booking 
    public isMissing: boolean;
 
    // Why the user missed the booking 
    public reason: string;
 
    // Entity Id 
    public id: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


/*
 *  Interface of Actual
*/
export interface IActual extends IBaseEntity {
 
    // Account Id 
    accountId?: string;
 
    // Booking Id 
    bookingId?: string;
 
    // User Id who registered for booking 
    userId?: string;
 
    // User name (for display only) 
    userName?: string;
 
    // Resource Id 
    resourceId?: string;
 
    // Resource name (for display only) 
    resourceName?: string;
 
    // Actual Duration (in minutes) 
    duration?: number;
 
    // Actual Distance (in meters) 
    distance?: number;
 
    // User missed the booking 
    isMissing?: boolean;
 
    // Why the user missed the booking 
    reason?: string;
 
    // Entity Id 
    id?: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    createdOn?: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    updatedOn?: number;
 
}
