import { Activity } from '../entities/Activity';
import { Booking } from '../entities/Booking';

/* 
 *  Activity Booking Group
 *  Group list of bookings by activity
*/
export class ActivityBookingGroup {
 
    // Activity 
    public activity: Activity;
 
    // List of bookings in the activity 
    public bookings: Booking[];
 
    constructor(activity?: Activity, bookings?: Booking[]) { 
        this.activity = activity;
        this.bookings = bookings;
    }

}


/*
 *  Interface of ActivityBookingGroup
*/
export interface IActivityBookingGroup {
 
    // Activity 
    activity?: Activity;
 
    // List of bookings in the activity 
    bookings?: Booking[];
 
}
