
/* 
 *  Count data point
*/
export class CountDataPoint {
 
    // Datapoint time [Epoch milliseconds Timestamp] 
    public timestamp: number;
 
    // Data point label 
    public label: string;
 
    // Data point value 
    public value: number;
 
    constructor(timestamp?: number, label?: string, value?: number) { 
        this.timestamp = timestamp;
        this.label = label;
        this.value = value;
    }

}


/*
 *  Interface of CountDataPoint
*/
export interface ICountDataPoint {
 
    // Datapoint time [Epoch milliseconds Timestamp] 
    timestamp?: number;
 
    // Data point label 
    label?: string;
 
    // Data point value 
    value?: number;
 
}
