import { ResourceTypeMask } from '../enums/ResourceTypeMask';
import { BookingStatusCode } from '../enums/BookingStatusCode';
import { BaseEntity } from '../entities/BaseEntity';
import { IBaseEntity } from '../entities/BaseEntity';

/* 
 *  Booking
 *  A booking is a pending request to book a resource for specific time for user(s), once it is approved, it becomes a placement
*/
export class Booking extends BaseEntity {
 
    // Activity id associated with this booking request 
    public activityId: string;
 
    // Activity name associated with this booking request 
    public activityName: string;
 
    // When to ask for placement [Epoch milliseconds Timestamp] 
    public placementOn: number;
 
    // Activity end time [Epoch milliseconds Timestamp] 
    public placementTo: number;
 
    // Booking for specific resource 
    public resourceId: string;
 
    // Resource name (for display only) 
    public resourceName: string;
 
    // User Id who requested the booking 
    public requestedBy: string;
 
    // User name (for display only) 
    public requestedByName: string;
 
    // User Ids registered for the placement 
    public requestedFor: string[];
 
    // Users name (for display only) 
    public requestedForNames: string[];
 
    // User Ids who actually participated the placement 
    public participants: string[];
 
    // Resource type 
    public resourceType: ResourceTypeMask;
 
    // When the placement was approved [Epoch milliseconds Timestamp] 
    public approvedOn: number;
 
    // Booking status 
    public status: BookingStatusCode;
 
    // Booking for a private resource 
    public private: boolean;
 
    // Actual Duration (in minutes) 
    public duration: number;
 
    // Actual Distance (in meters) 
    public distance: number;
 
    // General notes (mainly for accident) 
    public notes: string;
 
    // Entity Id 
    public id: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


/*
 *  Interface of Booking
*/
export interface IBooking extends IBaseEntity {
 
    // Activity id associated with this booking request 
    activityId?: string;
 
    // Activity name associated with this booking request 
    activityName?: string;
 
    // When to ask for placement [Epoch milliseconds Timestamp] 
    placementOn?: number;
 
    // Activity end time [Epoch milliseconds Timestamp] 
    placementTo?: number;
 
    // Booking for specific resource 
    resourceId?: string;
 
    // Resource name (for display only) 
    resourceName?: string;
 
    // User Id who requested the booking 
    requestedBy?: string;
 
    // User name (for display only) 
    requestedByName?: string;
 
    // User Ids registered for the placement 
    requestedFor?: string[];
 
    // Users name (for display only) 
    requestedForNames?: string[];
 
    // User Ids who actually participated the placement 
    participants?: string[];
 
    // Resource type 
    resourceType?: ResourceTypeMask;
 
    // When the placement was approved [Epoch milliseconds Timestamp] 
    approvedOn?: number;
 
    // Booking status 
    status?: BookingStatusCode;
 
    // Booking for a private resource 
    private?: boolean;
 
    // Actual Duration (in minutes) 
    duration?: number;
 
    // Actual Distance (in meters) 
    distance?: number;
 
    // General notes (mainly for accident) 
    notes?: string;
 
    // Entity Id 
    id?: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    createdOn?: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    updatedOn?: number;
 
}
