
/* 
*/
export class AdminResourceHistoryRequest {
 
    // Resource Id 
    public id: string;
 
    // 
    public from: number;
 
    // 
    public to: number;
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    constructor(id?: string, from?: number, to?: number, sort?: string) { 
        this.id = id;
        this.from = from;
        this.to = to;
        this.sort = sort;
    }

}


/*
 *  Interface of AdminResourceHistoryRequest
*/
export interface IAdminResourceHistoryRequest {
 
    // Resource Id 
    id?: string;
 
    // 
    from?: number;
 
    // 
    to?: number;
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    sort?: string;
 
}
