import { NotificationTypeCode } from '../enums/NotificationTypeCode';

/* 
*/
export class NotifyUserRequest {
 
    // List of user Ids 
    public userId: string[];
 
    // Notification Type 
    public type: NotificationTypeCode;
 
    // From time [Epoch milliseconds Timestamp] 
    public from: number;
 
    // To time [Epoch milliseconds Timestamp] 
    public to: number;
 
    // Notification Message 
    public body: string;
 
    constructor(userId?: string[], type?: NotificationTypeCode, from?: number, to?: number, body?: string) { 
        this.userId = userId;
        this.type = type;
        this.from = from;
        this.to = to;
        this.body = body;
    }

}


/*
 *  Interface of NotifyUserRequest
*/
export interface INotifyUserRequest {
 
    // List of user Ids 
    userId?: string[];
 
    // Notification Type 
    type?: NotificationTypeCode;
 
    // From time [Epoch milliseconds Timestamp] 
    from?: number;
 
    // To time [Epoch milliseconds Timestamp] 
    to?: number;
 
    // Notification Message 
    body?: string;
 
}
