import { DayOfWeekCode } from '../enums/DayOfWeekCode';
import { BaseEntity } from '../entities/BaseEntity';
import { IBaseEntity } from '../entities/BaseEntity';

/* 
 *  Usage is a daily statistics about the number of participants and resource usage
 *  Id is the day Id (in the form of: YYYYMMDD000000)
*/
export class Usage extends BaseEntity {
 
    // Day of week (for distribution) 
    public weekday: DayOfWeekCode;
 
    // Total number of participants 
    public count: number;
 
    // Number of singles 
    public single: number;
 
    // Number of doubles 
    public double: number;
 
    // Number of quads 
    public quad: number;
 
    // Number of eights 
    public eight: number;
 
    // Number of private single boats 
    public singlePrivate: number;
 
    // Entity Id 
    public id: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


/*
 *  Interface of Usage
*/
export interface IUsage extends IBaseEntity {
 
    // Day of week (for distribution) 
    weekday?: DayOfWeekCode;
 
    // Total number of participants 
    count?: number;
 
    // Number of singles 
    single?: number;
 
    // Number of doubles 
    double?: number;
 
    // Number of quads 
    quad?: number;
 
    // Number of eights 
    eight?: number;
 
    // Number of private single boats 
    singlePrivate?: number;
 
    // Entity Id 
    id?: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    createdOn?: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    updatedOn?: number;
 
}
