import { NotificationTypeCode } from '../enums/NotificationTypeCode';

/* 
*/
export class NotifyActivityUsersRequest {
 
    // Activity Id 
    public activityId: string;
 
    // Notification Type 
    public type: NotificationTypeCode;
 
    // From time [Epoch milliseconds Timestamp] 
    public from: number;
 
    // To time [Epoch milliseconds Timestamp] 
    public to: number;
 
    // Notification Message 
    public body: string;
 
    constructor(activityId?: string, type?: NotificationTypeCode, from?: number, to?: number, body?: string) { 
        this.activityId = activityId;
        this.type = type;
        this.from = from;
        this.to = to;
        this.body = body;
    }

}


/*
 *  Interface of NotifyActivityUsersRequest
*/
export interface INotifyActivityUsersRequest {
 
    // Activity Id 
    activityId?: string;
 
    // Notification Type 
    type?: NotificationTypeCode;
 
    // From time [Epoch milliseconds Timestamp] 
    from?: number;
 
    // To time [Epoch milliseconds Timestamp] 
    to?: number;
 
    // Notification Message 
    body?: string;
 
}
