import { Booking } from '../entities/Booking';

/* 
 *  Booking Group
 *  Group list of bookings by time
*/
export class BookingGroup {
 
    // Time for grouping [Epoch milliseconds Timestamp] 
    public timeGroup: number;
 
    // List of bookings in the group 
    public bookings: Booking[];
 
    constructor(timeGroup?: number, bookings?: Booking[]) { 
        this.timeGroup = timeGroup;
        this.bookings = bookings;
    }

}


/*
 *  Interface of BookingGroup
*/
export interface IBookingGroup {
 
    // Time for grouping [Epoch milliseconds Timestamp] 
    timeGroup?: number;
 
    // List of bookings in the group 
    bookings?: Booking[];
 
}
