export class NotifyBookingUsersRequest {
 
    // Booking Id 
    public bookingId: string;
 
    // Notification Type 
    public type: NotificationTypeCode;
 
    // From time [Epoch milliseconds Timestamp] 
    public from: number;
 
    // To time [Epoch milliseconds Timestamp] 
    public to: number;
 
    // Notification Message 
    public body: string;
 
    constructor(bookingId?: string, type?: NotificationTypeCode, from?: number, to?: number, body?: string) { 
        this.bookingId = bookingId;
        this.type = type;
        this.from = from;
        this.to = to;
        this.body = body;
    }

}


