
/* 
 *  MemberUsage is a time period statistics about the number of bookings
*/
export class MemberUsage {
 
    // Member Id 
    public id: string;
 
    // Member Name 
    public name: string;
 
    // Total number of activities in this period 
    public total: number;
 
    // Number activities on Sunday 
    public sun: number;
 
    // Number activities on Monday 
    public mon: number;
 
    // Number activities on Tuesday 
    public tue: number;
 
    // Number activities on Wednesday 
    public wed: number;
 
    // Number activities on Thursday 
    public thu: number;
 
    // Number activities on Friday 
    public fri: number;
 
    // Number activities on Saturday 
    public sat: number;
 
    // Number of absences in this period 
    public absences: number;
 
    // Number of accidents in this period 
    public accidents: number;
 
    constructor(id?: string, name?: string, total?: number, sun?: number, mon?: number, tue?: number, wed?: number, thu?: number, fri?: number, sat?: number, absences?: number, accidents?: number) { 
        this.id = id;
        this.name = name;
        this.total = total;
        this.sun = sun;
        this.mon = mon;
        this.tue = tue;
        this.wed = wed;
        this.thu = thu;
        this.fri = fri;
        this.sat = sat;
        this.absences = absences;
        this.accidents = accidents;
    }

}


/*
 *  Interface of MemberUsage
*/
export interface IMemberUsage {
 
    // Member Id 
    id?: string;
 
    // Member Name 
    name?: string;
 
    // Total number of activities in this period 
    total?: number;
 
    // Number activities on Sunday 
    sun?: number;
 
    // Number activities on Monday 
    mon?: number;
 
    // Number activities on Tuesday 
    tue?: number;
 
    // Number activities on Wednesday 
    wed?: number;
 
    // Number activities on Thursday 
    thu?: number;
 
    // Number activities on Friday 
    fri?: number;
 
    // Number activities on Saturday 
    sat?: number;
 
    // Number of absences in this period 
    absences?: number;
 
    // Number of accidents in this period 
    accidents?: number;
 
}
