import { TimeUnitCode } from '../enums/TimeUnitCode';

/* 
*/
export class UserBookingGroupRequest {
 
    // From booking time [Epoch milliseconds Timestamp] 
    public from: number;
 
    // To booking time [Epoch milliseconds Timestamp] 
    public to: number;
 
    // Group by time period 
    public groupBy: TimeUnitCode;
 
    constructor(from?: number, to?: number, groupBy?: TimeUnitCode) { 
        this.from = from;
        this.to = to;
        this.groupBy = groupBy;
    }

}


/*
 *  Interface of UserBookingGroupRequest
*/
export interface IUserBookingGroupRequest {
 
    // From booking time [Epoch milliseconds Timestamp] 
    from?: number;
 
    // To booking time [Epoch milliseconds Timestamp] 
    to?: number;
 
    // Group by time period 
    groupBy?: TimeUnitCode;
 
}
