
/* 
*/
export class AdminDailyPlaningRequest {
 
    // Planing day 
    public day: number;
 
    // Resource filter 
    public resFilter: string;
 
    // User filter 
    public userFilter: string;
 
    constructor(day?: number, resFilter?: string, userFilter?: string) { 
        this.day = day;
        this.resFilter = resFilter;
        this.userFilter = userFilter;
    }

}


/*
 *  Interface of AdminDailyPlaningRequest
*/
export interface IAdminDailyPlaningRequest {
 
    // Planing day 
    day?: number;
 
    // Resource filter 
    resFilter?: string;
 
    // User filter 
    userFilter?: string;
 
}
