import { Injectable, Inject } from '@angular/core';
import { RestUtil } from '../../utils/rest-util';
import { CoreConfig } from '../../config';

import { Booking } from '../entities/Booking';
import { TimeUnitCode } from '../enums/TimeUnitCode';


/**
 * Services for managing kayak resources - for account administrator only
 * @RequestHeader X-API-KEY The key to identify the application (portal)
 * @RequestHeader X-ACCESS-TOKEN The token to identify the logged-in user 
 */
@Injectable()
export class UserBookingsService {

  // URL to web api
  private baseUrl = '/user/bookings';

  /**
   * Class constructor
   */
  constructor(@Inject('config') private config: CoreConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Create new booking
   * @Return: EntityResponse<Booking>
   */
  create(body?: Booking) {
    return this.rest.post(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Update booking
   * @Return: EntityResponse<Booking>
   */
  update(body?: Booking) {
    return this.rest.put(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Update actual activity log (duration and distance)
   * @Return: EntityResponse<Booking>
   */
  updateLog(body?: Booking) {
    return this.rest.put(`${this.baseUrl}/log`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Delete booking
   * @Return: ActionResponse
   */
  delete(id?: string) {
    return this.rest.delete(`${this.baseUrl}/${id}`);
  }

  /**
   * Remove current user from booking
   * @Return: ActionResponse
   */
  removeMe(id?: string) {
    return this.rest.delete(`${this.baseUrl}/${id}/remove-me`);
  }

  /**
   * Get single booking by id
   * @Return: EntityResponse<Booking>
   */
  get(id?: string) {
    return this.rest.get(`${this.baseUrl}/${id}`);
  }

  /**
   * Get extended booking info by id including names
   * @Return: EntityResponse<Booking>
   */
  getExt(id?: string) {
    return this.rest.get(`${this.baseUrl}/${id}/ext`);
  }

  /**
   * Find bookings by filters
   * @Return: QueryResponse<Booking>
   */
  find(userId?: string, resourceId?: string, from?: number, to?: number, sort?: string, page?: number, pageSize?: number) {
    const params = new Array();		
    if (userId != null) { params.push(`userId=${userId}`); }
    if (resourceId != null) { params.push(`resourceId=${resourceId}`); }
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (pageSize != null) { params.push(`pageSize=${pageSize}`); }

		return this.rest.get(`${this.baseUrl}`, ...params);
  }

  /**
   * Find bookings daily / monthly / yearly history by filters
   * @Return: QueryResponse<Booking>
   */
  history(resourceId?: string, year?: number, month?: number, day?: number, sort?: string) {
    const params = new Array();		
    if (resourceId != null) { params.push(`resourceId=${resourceId}`); }
    if (year != null) { params.push(`year=${year}`); }
    if (month != null) { params.push(`month=${month}`); }
    if (day != null) { params.push(`day=${day}`); }
    if (sort != null) { params.push(`sort=${sort}`); }

		return this.rest.get(`${this.baseUrl}/history`, ...params);
  }

  /**
   * Group my bookings by time period
   * @Return: EntitiesResponse<BookingGroup>
   */
  groups(from?: number, to?: number, groupBy?: TimeUnitCode) {
    const params = new Array();		
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }
    if (groupBy != null) { params.push(`groupBy=${groupBy}`); }

		return this.rest.get(`${this.baseUrl}/groups`, ...params);
  }

  /**
   * Find list of activities groups by time period
   * @Return: EntitiesResponse<BookingGroup>
   */
  activities(from?: number, to?: number, groupBy?: TimeUnitCode) {
    const params = new Array();		
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }
    if (groupBy != null) { params.push(`groupBy=${groupBy}`); }

		return this.rest.get(`${this.baseUrl}/activities`, ...params);
  }

}
