/* 
   Booking status code 
*/
export enum BookingStatusCode {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // Pending booking request [1] 
    PENDING = 1,
 
    // Approved booking [2] 
    APPROVED = 2,
 
    // Completed booking [3] 
    COMPLETED = 3,
 
    // User absence from booking [4] 
    ABSENCE = 4,
 
    // User was involved in an accident [5] 
    ACCIDENT = 5,
 
}