import { QueryResponse } from '../messages/QueryResponse';
import { Activity } from '../entities/Activity';
import { IQueryResponse } from '../messages/QueryResponse';

/* 
*/
export class QueryResponseOfActivity extends QueryResponse {
 
    // Activity list 
    public list: Activity[];
 
    // Error code (0 for success) 
    public code: number;
 
    // Error message 
    public error: string;
 
    // Current page (Bulk) number 
    public page: number;
 
    // Size of page (items in bulk) 
    public pageSize: number;
 
    // Total number of pages 
    public pages: number;
 
    // Total number of items in the query 
    public total: number;
 
    // Query definition spec as Json string 
    public queryDef: string;
 
    // Entity type 
    public docType: string;
 

}


/*
 *  Interface of QueryResponseOfActivity
*/
export interface IQueryResponseOfActivity extends IQueryResponse {
 
    // Activity list 
    list?: Activity[];
 
    // Error code (0 for success) 
    code?: number;
 
    // Error message 
    error?: string;
 
    // Current page (Bulk) number 
    page?: number;
 
    // Size of page (items in bulk) 
    pageSize?: number;
 
    // Total number of pages 
    pages?: number;
 
    // Total number of items in the query 
    total?: number;
 
    // Query definition spec as Json string 
    queryDef?: string;
 
    // Entity type 
    docType?: string;
 
}
