import { RecurrentActivity } from '../common/RecurrentActivity';

/* 
 *  Account specific settings
*/
export class AccountSettings {
 
    // Default recurrent weekly activities 
    public defaultActivities: RecurrentActivity[];
 
    // Operational (dynamic) data retention time in days (1 - 365) 
    public retentionDays: number;
 
    constructor(defaultActivities?: RecurrentActivity[], retentionDays?: number) { 
        this.defaultActivities = defaultActivities;
        this.retentionDays = retentionDays;
    }

}


/*
 *  Interface of AccountSettings
*/
export interface IAccountSettings {
 
    // Default recurrent weekly activities 
    defaultActivities?: RecurrentActivity[];
 
    // Operational (dynamic) data retention time in days (1 - 365) 
    retentionDays?: number;
 
}
