/* 
   General Resource Type (Attributes bit mask) 
*/
export enum ResourceTypeMask {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // Rowing Boat [1] 
    RBOAT = 1,
 
    // Kayak [2] 
    KAYAK = 2,
 
    // Sculling (2 oars) [4] 
    SCULL = 4,
 
    // Need Cox [8] 
    COX = 8,
 
    // Wide [16] 
    WIDE = 16,
 
    // For competition [32] 
    COMP = 32,
 
    // For para-olympic [64] 
    PARA = 64,
 
    // Coastal (use in sea) [128] 
    COASTAL = 128,
 
    // Single [1024] 
    P1 = 1024,
 
    // Double [2048] 
    P2 = 2048,
 
    // Quad [4096] 
    P4 = 4096,
 
    // Eight [8192] 
    P8 = 8192,
 
}