import { BaseEntity } from '../entities/BaseEntity';
import { Resource } from '../entities/Resource';
import { Booking } from '../entities/Booking';
import { PlacementStatusCode } from '../enums/PlacementStatusCode';
import { IBaseEntity } from '../entities/BaseEntity';

/* 
 *  Placement is a structure to ap booking requests to a resource
*/
export class Placement extends BaseEntity {
 
    // Placement resource 
    public resource: Resource;
 
    // List of bookings 
    public bookings: Booking[];
 
    // Placement status 
    public status: PlacementStatusCode;
 
    // Entity Id 
    public id: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


/*
 *  Interface of Placement
*/
export interface IPlacement extends IBaseEntity {
 
    // Placement resource 
    resource?: Resource;
 
    // List of bookings 
    bookings?: Booking[];
 
    // Placement status 
    status?: PlacementStatusCode;
 
    // Entity Id 
    id?: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    createdOn?: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    updatedOn?: number;
 
}
