
/* 
*/
export class AdminActivityFindRequest {
 
    // From time [Epoch milliseconds Timestamp] 
    public from: number;
 
    // To time [Epoch milliseconds Timestamp] 
    public to: number;
 
    // Year 
    public year: number;
 
    // Month 
    public month: number;
 
    // Day 
    public day: number;
 
    // Include total bookings per activity? 
    public total: boolean;
 
    constructor(from?: number, to?: number, year?: number, month?: number, day?: number, total?: boolean) { 
        this.from = from;
        this.to = to;
        this.year = year;
        this.month = month;
        this.day = day;
        this.total = total;
    }

}


/*
 *  Interface of AdminActivityFindRequest
*/
export interface IAdminActivityFindRequest {
 
    // From time [Epoch milliseconds Timestamp] 
    from?: number;
 
    // To time [Epoch milliseconds Timestamp] 
    to?: number;
 
    // Year 
    year?: number;
 
    // Month 
    month?: number;
 
    // Day 
    day?: number;
 
    // Include total bookings per activity? 
    total?: boolean;
 
}
