import { UserGenderCode } from '../enums/UserGenderCode';
import { AccountRoleCode } from '../enums/AccountRoleCode';

/* 
 *  Member registration data model
*/
export class MemberRegistration {
 
    // User name 
    public name: string;
 
    // User email 
    public email: string;
 
    // User mobile phone number (for notification and validation) 
    public mobile: string;
 
    // User gender 
    public gender: UserGenderCode;
 
    // Default account Id 
    public accountId: string;
 
    // List of accounts and roles the user has access to 
    public accountRole: AccountRoleCode;
 
    // Temporary password (leave empty for system generated password) 
    public tempPassword: string;
 
    // User must change password on next login 
    public changePassword: boolean;
 
    // Flag to send email verification to the user 
    public verifyByEmail: boolean;
 
    constructor(name?: string, email?: string, mobile?: string, gender?: UserGenderCode, accountId?: string, accountRole?: AccountRoleCode, tempPassword?: string, changePassword?: boolean, verifyByEmail?: boolean) { 
        this.name = name;
        this.email = email;
        this.mobile = mobile;
        this.gender = gender;
        this.accountId = accountId;
        this.accountRole = accountRole;
        this.tempPassword = tempPassword;
        this.changePassword = changePassword;
        this.verifyByEmail = verifyByEmail;
    }

}


/*
 *  Interface of MemberRegistration
*/
export interface IMemberRegistration {
 
    // User name 
    name?: string;
 
    // User email 
    email?: string;
 
    // User mobile phone number (for notification and validation) 
    mobile?: string;
 
    // User gender 
    gender?: UserGenderCode;
 
    // Default account Id 
    accountId?: string;
 
    // List of accounts and roles the user has access to 
    accountRole?: AccountRoleCode;
 
    // Temporary password (leave empty for system generated password) 
    tempPassword?: string;
 
    // User must change password on next login 
    changePassword?: boolean;
 
    // Flag to send email verification to the user 
    verifyByEmail?: boolean;
 
}
