
/* 
*/
export class AdminDailyRegistrationRequest {
 
    // Activity day 
    public day: number;
 
    // Filter results by user name 
    public filter: string;
 
    // Filter results by missing only users 
    public missingOnly: boolean;
 
    constructor(day?: number, filter?: string, missingOnly?: boolean) { 
        this.day = day;
        this.filter = filter;
        this.missingOnly = missingOnly;
    }

}


/*
 *  Interface of AdminDailyRegistrationRequest
*/
export interface IAdminDailyRegistrationRequest {
 
    // Activity day 
    day?: number;
 
    // Filter results by user name 
    filter?: string;
 
    // Filter results by missing only users 
    missingOnly?: boolean;
 
}
