import { TimeFrame } from '../common/TimeFrame';
import { NotificationTypeCode } from '../enums/NotificationTypeCode';
import { BaseEntity } from '../entities/BaseEntity';
import { IBaseEntity } from '../entities/BaseEntity';

/* 
 *  Notification
*/
export class Notification extends BaseEntity {
 
    // User Id 
    public userId: string;
 
    // Timeframe for the notification to be visible [Epoch milliseconds Timestamp] 
    public notifyOn: TimeFrame;
 
    // Message to show 
    public message: string;
 
    // Notification type 
    public type: NotificationTypeCode;
 
    // When the message has been seen by the user [Epoch milliseconds Timestamp] 
    public acceptedOn: number;
 
    // Entity Id 
    public id: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


/*
 *  Interface of Notification
*/
export interface INotification extends IBaseEntity {
 
    // User Id 
    userId?: string;
 
    // Timeframe for the notification to be visible [Epoch milliseconds Timestamp] 
    notifyOn?: TimeFrame;
 
    // Message to show 
    message?: string;
 
    // Notification type 
    type?: NotificationTypeCode;
 
    // When the message has been seen by the user [Epoch milliseconds Timestamp] 
    acceptedOn?: number;
 
    // Entity Id 
    id?: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    createdOn?: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    updatedOn?: number;
 
}
