
/* 
 *  Key Value string-int tuple
*/
export class StringIntValue {
 
    // Key of tuple 
    public key: string;
 
    // Int Value of tuple 
    public value: number;
 
    constructor(key?: string, value?: number) { 
        this.key = key;
        this.value = value;
    }

}


/*
 *  Interface of StringIntValue
*/
export interface IStringIntValue {
 
    // Key of tuple 
    key?: string;
 
    // Int Value of tuple 
    value?: number;
 
}
