import { BaseEntity } from '../entities/BaseEntity';
import { Placement } from '../entities/Placement';
import { Booking } from '../entities/Booking';
import { IBaseEntity } from '../entities/BaseEntity';

/* 
 *  Planing is a set of booking request and placements
*/
export class Planing extends BaseEntity {
 
    // Activity Id 
    public activityId: string;
 
    // Start time [Epoch milliseconds Timestamp] 
    public from: number;
 
    // End time [Epoch milliseconds Timestamp] 
    public to: number;
 
    // List of placements 
    public placements: Placement[];
 
    // List of bookings 
    public bookings: Booking[];
 
    // Entity Id 
    public id: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


/*
 *  Interface of Planing
*/
export interface IPlaning extends IBaseEntity {
 
    // Activity Id 
    activityId?: string;
 
    // Start time [Epoch milliseconds Timestamp] 
    from?: number;
 
    // End time [Epoch milliseconds Timestamp] 
    to?: number;
 
    // List of placements 
    placements?: Placement[];
 
    // List of bookings 
    bookings?: Booking[];
 
    // Entity Id 
    id?: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    createdOn?: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    updatedOn?: number;
 
}
