import { NotificationTypeCode } from '../enums/NotificationTypeCode';

/* 
*/
export class FindNotificationsRequest {
 
    // Free text search on message 
    public search: string;
 
    // Filter by notification type (UNDEFINED means no filter) 
    public type: NotificationTypeCode;
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    // Page number (for pagination) 
    public page: number;
 
    // Number of items per page (for pagination) 
    public pageSize: number;
 
    constructor(search?: string, type?: NotificationTypeCode, sort?: string, page?: number, pageSize?: number) { 
        this.search = search;
        this.type = type;
        this.sort = sort;
        this.page = page;
        this.pageSize = pageSize;
    }

}


/*
 *  Interface of FindNotificationsRequest
*/
export interface IFindNotificationsRequest {
 
    // Free text search on message 
    search?: string;
 
    // Filter by notification type (UNDEFINED means no filter) 
    type?: NotificationTypeCode;
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    sort?: string;
 
    // Page number (for pagination) 
    page?: number;
 
    // Number of items per page (for pagination) 
    pageSize?: number;
 
}
