import { NotificationTypeCode } from '../enums/NotificationTypeCode';

/* 
*/
export class NotifyDailyUsersRequest {
 
    // Day Id 
    public dayId: number;
 
    // Notification Type 
    public type: NotificationTypeCode;
 
    // From time [Epoch milliseconds Timestamp] 
    public from: number;
 
    // To time [Epoch milliseconds Timestamp] 
    public to: number;
 
    // Notification Message 
    public body: string;
 
    constructor(dayId?: number, type?: NotificationTypeCode, from?: number, to?: number, body?: string) { 
        this.dayId = dayId;
        this.type = type;
        this.from = from;
        this.to = to;
        this.body = body;
    }

}


/*
 *  Interface of NotifyDailyUsersRequest
*/
export interface INotifyDailyUsersRequest {
 
    // Day Id 
    dayId?: number;
 
    // Notification Type 
    type?: NotificationTypeCode;
 
    // From time [Epoch milliseconds Timestamp] 
    from?: number;
 
    // To time [Epoch milliseconds Timestamp] 
    to?: number;
 
    // Notification Message 
    body?: string;
 
}
