import { ResourceTypeMask } from '../enums/ResourceTypeMask';
import { AccountRoleCode } from '../enums/AccountRoleCode';
import { BaseEntity } from '../entities/BaseEntity';
import { ActivityStatusCode } from '../enums/ActivityStatusCode';
import { IBaseEntity } from '../entities/BaseEntity';

/* 
 *  Daily account activities entity
*/
export class Activity extends BaseEntity {
 
    // Activity name 
    public name: string;
 
    // Start time [Epoch milliseconds Timestamp] 
    public from: number;
 
    // End time [Epoch milliseconds Timestamp] 
    public to: number;
 
    // Status of booking requests 
    public status: ActivityStatusCode;
 
    // Activity is restricted to specific resource types 
    public resourceFilter: ResourceTypeMask[];
 
    // Activity is restricted to specific roles (members, para, team) 
    public roleFilter: AccountRoleCode[];
 
    // Message for the activity 
    public message: string;
 
    // Max participants in the activity 
    public limit: number;
 
    // Entity Id 
    public id: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


/*
 *  Interface of Activity
*/
export interface IActivity extends IBaseEntity {
 
    // Activity name 
    name?: string;
 
    // Start time [Epoch milliseconds Timestamp] 
    from?: number;
 
    // End time [Epoch milliseconds Timestamp] 
    to?: number;
 
    // Status of booking requests 
    status?: ActivityStatusCode;
 
    // Activity is restricted to specific resource types 
    resourceFilter?: ResourceTypeMask[];
 
    // Activity is restricted to specific roles (members, para, team) 
    roleFilter?: AccountRoleCode[];
 
    // Message for the activity 
    message?: string;
 
    // Max participants in the activity 
    limit?: number;
 
    // Entity Id 
    id?: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    createdOn?: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    updatedOn?: number;
 
}
