import { DayOfWeekCode } from '../enums/DayOfWeekCode';

/* 
 *  Schedule recurrent time frame
*/
export class RecurrentTimeFrame {
 
    // Day of week - based on ISO8601 
    public dayOfWeek: DayOfWeekCode;
 
    // Start time [number of minutes start from 00:00] 
    public startTime: number;
 
    // End time [number of minutes start from 00:00] 
    public endTime: number;
 
    // Name of the time frame 
    public name: string;
 
    constructor(dayOfWeek?: DayOfWeekCode, startTime?: number, endTime?: number, name?: string) { 
        this.dayOfWeek = dayOfWeek;
        this.startTime = startTime;
        this.endTime = endTime;
        this.name = name;
    }

}


/*
 *  Interface of RecurrentTimeFrame
*/
export interface IRecurrentTimeFrame {
 
    // Day of week - based on ISO8601 
    dayOfWeek?: DayOfWeekCode;
 
    // Start time [number of minutes start from 00:00] 
    startTime?: number;
 
    // End time [number of minutes start from 00:00] 
    endTime?: number;
 
    // Name of the time frame 
    name?: string;
 
}
